.e-sports {
	&__slider {
		display: none;
		visibility: hidden;
		&.slick-initialized {
			display: block;
			visibility: visible;
		}
		.slick-list {
			mask-image: linear-gradient(90deg,#fff,#fff 2%,#fff 98%,hsla(0deg,0%,100%,0));
		}
		.slick-slide {
			padding: 0 5px;
		}
	}
	&__item {
		display: block;
		width: 160px;
	}
	&__img-box {
		position: relative;
		display: block;
		width: 100%;
		height: 0;
		padding-bottom: 150%;
		overflow: hidden;
	}
	&__img {
		position: absolute;
		top: 0;
		left: 0;
		display: block;
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
	&__info-box {
		display: block;
		padding: 10px 10px 15px;
		width: 100%;
		height: 120px;
		font-size: 12px;
		text-align: center;
		background: var(--white-010);
		text-shadow: 0 1px 1px rgba(0, 0, 0, 0.6);
	}
	// &__catagory {
	// }
	&__event {
		display: block;
		margin-bottom: 10px;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
	}
	&__date {
		position: relative;
		display: block;
		margin-top: 10px;
		font-weight: 700;
		&--live {
			&::before {
				content: "";
				display: block;
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-320%, -50%);
				width: 8px;
				height: 8px;
				border-radius: 50%;
				background: var(--red);
			}
		}
	}
