.them-filter {
	--link-color: var(--gray);
	position: relative;
	z-index: 2;
	display: block;
	width: 100%;
	&__row {
		display: flex;
		justify-content: center;
		width: 100%;
		border-bottom: 1px solid var(--white-020);
	}
	&__list {
		display: flex;
		justify-content: flex-start;
		align-items: flex-start;
		width: auto;
		text-align: center;
		overflow-y: hidden;
		overflow-x: auto;
	}
	&__item {
		position: relative;
		display: inline-flex;
		justify-content: center;
		align-items: center;
	}
	&__link {
		display: inline-flex;
		justify-content: center;
		align-items: center;
		gap: 10px;
		padding: 0 20px;
		height: 40px;
		text-shadow: 0 1px 1px rgb(0 0 0 / 60%);
		background: var(--black-050);
		border-bottom: 2px solid transparent;
		&:hover,
		&--active {
			--link-color: var(--white);
			background: var(--white-010);
		}
		&--active {
			border-bottom-color: var(--white);
		}
		&--sub-menu {
			.them-filter__link-info {
				position: relative;
				padding-right: 25px;
				&::after {
					content: "";
					display: block;
					position: absolute;
					top: 50%;
					right: 5px;
					transform: translateY(-25%);
					width: 0;
					height: 0;
					border-style: solid;
					border-width: 4px 4px 0 4px;
					border-color: var(--white) transparent transparent
						transparent;
				}
			}
			&:hover,
			&--active {
				.them-filter__link-info {
					padding-right: 25px;
				}
			}
		}
	}
	&__link-img-box {
		display: block;
		width: 40px;
		height: 40px;
	}
	&__link-img {
		display: block;
		width: 40px;
		height: 40px;
		object-fit: cover;
	}
	&__link-info {
		display: inline-block;
		font-size: 12px;
		font-weight: 700;
		color: var(--link-color);
		white-space: nowrap;
		text-transform: uppercase;
	}
	&__sub-menu {
		--sub-menu-bg: var(--black-dark);
		display: none;
		visibility: hidden;
		background: var(--sub-menu-bg);
		&--active {
			display: block;
			visibility: visible;
		}
	}
	&__catalog {
		display: none;
		visibility: hidden;
		&--active {
			display: block;
			visibility: visible;
		}
	}
	&__game-section {
		color: var(--white);
		background: var(--bg-color);
	}
	.provider-catalog {
		border-bottom: 1px solid var(--white-020);
	}
	@media screen and (min-width: 1200px) {
		&__link-info {
			font-size: 16px;
		}
	}
}

.them-catalog {
	display: block;
	width: 100%;
	border-bottom: 1px solid var(--white-020);
	&__row {
		display: flex;
		justify-content: center;
		width: 100%;
	}
	&__list {
		display: grid;
		grid-template-columns: repeat(auto-fill, 130px);
		justify-content: center;
		&--scroll {
			justify-content: start;
			grid-template-columns: repeat(10, 130px);
			grid-template-rows: 120px 120px;
			grid-auto-flow: column;
			overflow-x: auto;
			-webkit-mask-image: linear-gradient(
				90deg,
				#fff,
				#fff 3%,
				#fff 97%,
				hsla(0deg, 0%, 100%, 0)
			);
			mask-image: linear-gradient(
				90deg,
				#fff,
				#fff 3%,
				#fff 97%,
				hsla(0deg, 0%, 100%, 0)
			);
		}
	}
	&__item {
		display: block;
	}
	&__link {
		position: relative;
		display: inline-flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		padding: 15px 0 0;
		width: 130px;
		&:hover,
		&--active {
			.them-catalog {
				&__link-img {
					box-shadow: 0px 0px 0px 4px var(--decor-color);
				}
				&__link-info {
					color: var(--font-color);
					background: var(--decor-color);
					transform: translateY(-10px);
				}
			}
		}
	}
	&__link-img {
		display: block;
		width: 70px;
		height: 70px;
		object-fit: cover;
	}
	&__link-info {
		display: inline-flex;
		justify-content: center;
		align-items: center;
		padding: 0 10px;
		height: 35px;
		font-size: 12px;
		font-weight: 700;
		color: var(--white);
		text-shadow: 0 1px 1px rgb(0 0 0 / 60%);
		transition: transform 0.2s ease-in-out;
		span {
			&::first-letter {
				text-transform: capitalize;
			}
		}
	}
}

.provider-catalog {
	--link-img-bg: var(--black-050);
	display: block;
	&__grid {
		display: grid;
		grid-template-columns: repeat(auto-fill, minmax(160px, 1fr));
		justify-content: center;
		gap: 0 20px;
		padding: 10px 0;
	}
	&__slider {
		--link-img-bg: transparent;
		display: none;
		visibility: hidden;
		&.slick-initialized {
			display: block;
			visibility: visible;
		}

		.slick-list {
			padding: 0 40px;
			mask-image: linear-gradient(
				90deg,
				hsla(0, 0%, 100%, 0),
				#fff 3%,
				#fff 97%,
				hsla(0, 0%, 100%, 0)
			);
			@media screen and (min-width: 1300px) {
				mask-image: linear-gradient(
					90deg,
					hsla(0, 0%, 100%, 0),
					#fff 3%,
					#fff 97%,
					hsla(0, 0%, 100%, 0)
				);
			}
			@media screen and (min-width: 1600px) {
				mask-image: linear-gradient(
					90deg,
					hsla(0, 0%, 100%, 0),
					#fff 5%,
					#fff 95%,
					hsla(0, 0%, 100%, 0)
				);
			}
		}
		.provider-catalog {
			&__item {
				padding: 0 5px;
			}
			&__link {
				height: 50px;
				&:hover,
				&--active {
					.provider-catalog {
						&__link-img-box {
							--link-img-bg: var(--white-020);
						}
					}
				}
			}
			&__link-info {
				display: none;
				visibility: hidden;
			}
		}
	}
	&__item {
		display: flex;
		justify-content: center;
		align-items: center;
	}
	&__link {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		width: 100%;
		cursor: pointer;
		&:hover,
		&--active {
			.provider-catalog {
				&__link-img-box {
					--link-img-bg: var(--white-020);
				}
				&__link-info {
					color: var(--font-color);
					background: var(--decor-color);
					transform: translateY(-10px);
				}
			}
		}
	}
	&__link-img-box {
		display: flex;
		justify-content: center;
		align-items: center;
		max-width: 200px;
		width: 100%;
		min-width: 160px;
		height: 70px;
		background: var(--link-img-bg);
		&--small {
			height: 50px;
		}
	}
	&__link-img {
		display: block;
		max-width: 160px;
		width: 80%;
		max-height: 50px;
		height: auto;
	}
	&__link-info {
		display: inline-flex;
		justify-content: center;
		align-items: center;
		margin-top: -10px;
		padding: 0 10px;
		width: auto;
		min-width: 100px;
		height: 35px;
		font-size: 12px;
		font-weight: 700;
		color: var(--white);
		text-shadow: 0 1px 1px rgb(0 0 0 / 60%);
		transition: transform 0.2s ease-in-out;
	}
}
