.main-slider {
	--info-bg: var(--white);
	--info-border: var(--white-dark);
	&__slider {
		perspective: 1000px;
		background: transparent;
		.main-slider {
			&__img-link {
				transform: rotateY(90deg);
				transition: transform 0.6s ease-in-out;
			}
			&__info-box {
				transform: translateY(100%);
				transition: transform 0.6s ease-in-out;
			}
		}
		.slick-slide {
			&.slick-current {
				.main-slider {
					&__img-link {
						transform: rotateY(0);
						transition-delay: 0.3s;
					}
					&__info-box {
						transform: translateY(0);
						transition-delay: 0.3s;
					}
				}
			}
		}
	}
	&__item {
		position: relative;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		// min-height: 490px;
	}
	&__item-bg {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		height: 400px;
		object-fit: cover;
		mask-image: linear-gradient(to bottom, rgba(30,87,153,1) 0%,rgba(101,161,212,1) 90%,rgba(113,173,222,0.7) 96%,rgba(125,185,232,0) 100%);
		z-index: 1;
	}
	&__img-side {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: center;
		max-width: 700px;
		width: 100%;
		z-index: 2;
	}
	&__img-link {
		display: inline-flex;
		justify-content: center;
		align-items: center;
	}
	&__img {
		display: block;
		max-width: 700px;
		width: 100%;
		max-height: 490px;
		height: auto;
	}
	&__info-side {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: center;
		margin-top: -25px;
		padding: 0 50px;
		max-width: 600px;
		width: 100%;
		z-index: 3;
	}
	&__info-box {
		flex: 1;
		position: relative;
		display: block;
		padding: 35px 5px;
		max-width: 600px;
		width: 100%;
		font-size: 24px;
		color: var(--font-color);
		text-align: center;
		// background: var(--info-bg);
		// border-radius: 35px;
		// border: 10px solid var(--info-border);
		// box-shadow: 0px 0px 0px 7px var(--info-bg);
	}
	&__info {
		text-shadow: 0px 2px 1px rgba(0, 0, 0, 0.6);
		&--big {
			font-size: 3.333em;
			line-height: 1;
			font-weight: 700;
		}
	}
	&__btn-box {
		display: flex;
		justify-content: center;
		align-items: center;
		flex-wrap: wrap;
		gap: 10px;
		margin-top: 15px;
	}
	@media screen and (min-width: 760px) {
		margin-bottom: -40px;
		&__item {
			flex-direction: row-reverse;
			gap: 10px;
		}
		&__img-side {
			flex: 1;
		}
		&__info-side {
			flex: 1;
			margin-top: 0;
			padding-bottom: 40px;
			text-align: left;
		}
		&__info-box {
			text-align: left;
		}
		&__btn-box {
			justify-content: flex-start;
		}
	}
	@media screen and (min-width: 990px) {
		&__item {
			padding-left: 25px;
			padding-right: 25px;
			gap: 20px;
		}
	}
	@media screen and (min-width: 1200px) {
		&__item {
			padding-left: 40px;
			padding-right: 40px;
		}
		&__info-box {
			padding: 35px 20px;
		}
	}
	@media screen and (min-width: 1600px) {
		&__info-side {
			align-self: flex-end;
		}
		&__info-box {
			padding: 60px 20px;
			font-size: 35px;
		}
	}
	@media screen and (min-width: 1900px) {
		&__info-side {
			max-width: 700px;
		}
		&__info-box {
			padding: 60px 10px;
			font-size: 40px;
		}
	}
}
