// 4 Game Item
.game-item {
	position: relative;
	display: inline-block;
	max-width: 300px;
	width: 100%;
	user-select: none;
	&__widget-trigger {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		z-index: 4;
	}
	&__box {
		position: relative;
		display: block;
		width: 100%;
		z-index: 1;
	}
	&__label {
		position: absolute;
		top: 0;
		display: inline-flex;
		justify-content: center;
		align-items: center;
		width: 40px;
		height: 40px;
		z-index: 4;
		pointer-events: none;
		&--new {
			right: 0;
			transform: translate(10px, -10px);
		}
		&--jackpot {
			left: 0;
			transform: translate(-10px, -10px);
		}
	}
	&__label-img {
		display: block;
		width: 40px;
		height: 40px;
	}
	&__img-box {
		position: relative;
		width: 100%;
		background: var(--white-010);
		&:after {
			content: attr(data-title);
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			display: flex;
			justify-content: center;
			align-items: center;
			padding: 10px;
			color: var(--white);
			font-size: 16px;
			font-weight: 700;
			text-align: center;
			z-index: 1;
			pointer-events: none;
		}
	}
	&__img-container {
		position: relative;
		display: block;
		width: 100%;
		height: 0;
		padding-bottom: 66%;
	}
	&__game-img {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		z-index: 2;
	}
	&__btn-box {
		position: absolute;
		top: 0;
		left: 0;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		width: 100%;
		height: 100%;
		background: var(--black-080);
		z-index: 3;
		opacity: 0;
		pointer-events: none;
		transition: opacity 0.3s ease-in-out;
	}
	&__btn-row {
		display: flex;
		justify-content: center;
		align-items: center;
		gap: 10px;
		padding-top: 15px;
	}
	&__jackpot {
		position: relative;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		margin-top: -14px;
		z-index: 3;
	}
	&__jackpot-img-box {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 40px;
		height: 24px;
	}
	&__jackpot-img {
		display: block;
		width: 40px;
		height: 40px;
	}
	&__jackpot-info {
		--jackpot-font-color: var(--font-color);
		--jackpot-bg-color: var(--decor-color);
		display: inline-flex;
		justify-content: center;
		align-items: center;
		padding: 0 8px;
		width: auto;
		height: 24px;
		font-size: 14px;
		font-weight: 700;
		color: var(--jackpot-font-color);
		background: var(--jackpot-bg-color);
		text-shadow: 0 1px 1px rgba(0, 0, 0,0.6);
	}
	&__title {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		height: 20px;
		font-size: 12px;
		font-weight: 700;
		letter-spacing: 0.05em;
		color: var(--white);
	}
	@media screen and (min-width: 1200px) {
		&:hover {
			.game-item {
				&__btn-box {
					opacity: 1;
					pointer-events: all;
				}
			}
		}
		&__widget-trigger {
			display: none;
			visibility: hidden;
		}
		&__img-box {
			&:after {
				font-size: 24px;
			}
		}
	}
}
