.payment-catalog {
	display: block;
	width: 100%;
	background: var(--black-dark);
	&__grid {
		display: grid;
		grid-template-columns: repeat(auto-fill, minmax(160px, 1fr));
		justify-items: center;
		justify-content: center;
		gap: 10px;
		padding: 10px 0;
	}
	@media screen and (min-width: 760px) {
		&__grid {
			grid-template-columns: repeat(auto-fill, 250px);
		}
	}
}
.payment-card {
	order: 2;
	position: relative;
	display: block;
	margin: 0 auto;
	padding: 10px;
	max-width: 250px;
	width: 100%;
	min-width: 160px;
	background: var(--white-010);
	&:hover {
		background: var(--white-020);
		.payment-card {
			&__title-img-box {
				background: var(--white-005);
			}
			&__title-info {
				color: var(--font-color);
				background: var(--gray);
			}
		}
		// .table {
		// 	--table-color: var(--font-color);
		// 	--table-bg: var(--white-dark);
		// 	--table-shadow: var(--white);
		// }
	}
	&--active,
	&--form {
		order: 1;
		grid-column-start: 1;
		grid-column-end: -1;
		.payment-card {
			&__title-img-box {
				background: var(--white-dark);
			}
			&__info-box {
				display: block;
				visibility: visible;
			}
			&__title-info {
				color: var(--font-color);
				background: var(--decor-color);
				box-shadow: 0px 4px 10px 0px rgba(0, 0, 0, 0.1);
			}
		}
	}
	&--form {
		background: none;
		&:hover {
			background: none;
			.payment-card {
				&__title {
					cursor: auto;
				}
				&__title-img-box {
					background: var(--white-dark);
				}
				&__title-info {
					background: var(--decor-color);
				} 
			}
		}
	}
	&__radio-input {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		z-index: 2;
		opacity: 0;
		&:hover ~ .payment-card__body {
			.payment-card {
				&__title-img-box {
					background: var(--white);
				}
				&__title-info {
					color: var(--font-color);
					background: var(--gray);
				}
			}
			// .table {
			// 	--table-color: var(--font-color);
			// 	--table-bg: var(--white-dark);
			// 	--table-shadow: var(--white);
			// }
		}
		&:checked {
			&:hover ~ .payment-card__body {
				.payment-card {
					&__title-info {
						background: var(--decor-color);
					}
				}
			}
			& ~ .payment-card__body {
				.payment-card {
					&__title-img-box {
						background: var(--white-dark);
					}
					&__title-info {
						color: var(--font-color);
						background: var(--decor-color);
						box-shadow: 0px 4px 10px 0px rgba(0, 0, 0, 0.1);
					}
					&__info-box {
						display: block;
						visibility: visible;
					}
				}
			}
		}
	}
	&__body {
		position: relative;
		text-shadow: 0 1px 1px rgba(0, 0, 0, 0.6);
		z-index: 1;
	}
	&__title {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		width: 100%;
		cursor: pointer;
	}
	&__title-img-box {
		display: flex;
		justify-content: center;
		align-items: center;
		// max-width: 200px;
		width: 100%;
		min-width: 140px;
		height: 70px;
		background: var(--white-005);
	}
	&__title-img {
		display: block;
		max-width: 160px;
		width: 80%;
		max-height: 50px;
		height: auto;
	}
	&__title-info {
		display: inline-flex;
		justify-content: center;
		align-items: center;
		margin-top: -10px;
		padding: 0 10px;
		width: auto;
		min-width: 100px;
		height: 35px;
		font-size: 12px;
		font-weight: 700;
		color: var(--white-dark);
		transition: transform 0.2s ease-in-out;
	}
	&__info-box {
		display: none;
		visibility: hidden;
		font-size: 12px;
	}
	&__btn-box {
		display: flex;
		justify-content: center;
		align-items: center;
		flex-wrap: wrap;
		gap: 10px;
		padding: 10px 0;
		font-size: 12px;
	}
	// .table {
	// 	--table-color: var(--white-dark);
	// 	--table-bg: var(--black);
	// 	--table-shadow: var(--black-dark);
	// 	// font-size: 10px;
	// }
	@media screen and (min-width: 760px) {
		&:hover {
			.payment-card {
				&__title-info {
					color: var(--font-color);
					background: var(--gray);
				}
			}
		}
		&--active {
			grid-column-start: 1;
			grid-column-end: 2;
		}
		&__radio-input {
			&:hover ~ .payment-card__body {
				.payment-card {
					&__title-info {
						color: var(--font-color);
						background: var(--gray);
					}
				}
			}
		}
		&__info-box {
			display: block;
			visibility: visible;
		}
	}
}
