.footer {
	--footer-padding-bottom: 100px;
	// --footer-padding-x: 10px;
	// --footer-bg: var(--bg-color);
	--footer-color: var(--white-dark);
	display: block;
	color: var(--footer-color);
	// background: var(--footer-bg);
	&__padding-bottom {
		padding-bottom: var(--footer-padding-bottom);
	}
	&__nav {
		margin-bottom: 15px;
	}
	&__language {
		display: block;
		margin-bottom: 30px;
	}
	&__contacts {
		display: block;
		margin-bottom: 20px;
	}
	
	&__partners {
		display: block;
	}
	&__info-box {
		display: block;
		margin-bottom: 15px;
		padding: 10px 0;
		font-size: 10px;
		line-height: 2;
		text-align: center;
		border-top: 2px solid var(--white-020);
	}
	&__service-box {
		display: block;
		margin-bottom: 10px;
	}
	// &__rights {
	// }
	@media screen and (min-width: 600px) {
		--footer-padding-bottom: 0;
	}
}
.footer-language {
	--footer-language-bg: var(--black-dark);
	--footer-language-color: var(--white-dark);
	display: flex;
	justify-content: center;
	align-items: center;
	margin: 0 auto;
	padding: 0 40px;
	width: 100%;
	background: var(--footer-language-bg);
	&__list {
		display: flex;
		justify-content: center;
		align-items: center;
		flex-wrap: wrap;
		gap: 20px;
	}
	&__item {
		flex: 1;
	}
	&__link {
		display: inline-flex;
		justify-content: center;
		align-items: center;
		gap: 10px;
		padding: 6px;
		font-size: 12px;
		font-weight: 700;
		color: var(--footer-language-color);
		&--current,
		&:hover {
			--footer-language-color: var(--decor-color);
		}
	}
	&__link-img {
		display: block;
		width: 24px;
		height: 18px;
	}
	&__link-info {
		display: inline-block;
		&::first-letter {
			text-transform: capitalize;
		}
	}
}
.footer-contacts {
	--footer-contacts-link-color: var(--white-dark);
	&__title {
		display: block;
		margin-bottom: 15px;
		font-size: 12px;
		color: var(--gray-light);
		text-align: center;
	}
	&__list {
		display: flex;
		justify-content: center;
		align-items: center;
		flex-wrap: wrap;
		gap: 10px;
	}
	// &__item {
	// }
	&__link {
		display: inline-flex;
		justify-content: center;
		align-items: center;
		gap: 5px;
		padding: 0 10px;
		font-size: 12px;
		font-weight: 700;
		color: var(--footer-contacts-link-color);
		&:hover {
			color: var(--decor-color);
			.footer-contacts__link-img {
				opacity: 1;
			}
		}
	}
	&__link-img {
		display: block;
		max-width: 30px;
		width: 30px;
		height: auto;
		opacity: 0.6;
	}
	&__link-info {
		&:empty {
			display: none;
			visibility: hidden;
		}
	}
}
.footer-nav {
	--footer-nav-color: var(--white-dark);
	--footer-nav-bg: var(--black-050);
	display: block;
	padding: 10px 0;
	background: var(--footer-nav-bg);
	border-bottom: 2px solid var(--white-020);
	&__list {
		display: flex;
		justify-content: center;
		align-items: center;
		flex-wrap: wrap;
		row-gap: 5px;
	}
	&__item {
		position: relative;
		&::after {
			content: "|";
			display: block;
			position: absolute;
			top: 50%;
			right: 0;
			transform: translateY(-50%);
			font-size: 12px;
			font-weight: 700;
			color: var(--gray-dark);
		}
		&:last-child {
			&::after {
				content: none;
			}
		}
	}
	&__link {
		display: inline-flex;
		justify-content: center;
		align-items: center;
		gap: 5px;
		padding: 5px 20px;
		min-height: 30px;
		font-size: 12px;
		font-weight: 700;
		color: var(--footer-nav-color);
		&:hover,
		&--current {
			color: var(--decor-color);
		}
	}
	&__link-img {
		&:empty {
			display: none;
			visibility: hidden;
		}
	}
	&__link-info {
		&:empty {
			display: none;
			visibility: hidden;
		}
		&::first-letter {
			text-transform: capitalize;
		}
	}
}
.footer-partners {
	display: block;
	&__slider {
		display: none;
		visibility: hidden;
		&.slick-initialized {
			display: block;
			visibility: visible;
		}
	}
	&__item {
		display: inline-flex;
		justify-content: center;
		align-items: center;
	}
	&__link {
		display: inline-flex;
		justify-content: center;
		align-items: center;
		padding: 25px 15px 15px;
		filter: grayscale(1);
		&:hover {
			filter: grayscale(0);
		}
	}
	&__link-img {
		display: block;
		width: auto;
		height: 30px;
	}
	&__link-info {
		&:empty {
			display: none;
			visibility: hidden;
		}
	}
}
.footer-service {
	display: block;
	&__list {
		display: flex;
		justify-content: center;
		align-items: center;
		flex-wrap: wrap;
		gap: 10px;
	}
	// &__item {
	// }
	&__link {
		display: inline-flex;
		justify-content: center;
		align-items: center;
		gap: 5px;
	}
	&__link-img {
		display: block;
		max-height: 36px;
		width: auto;
		// &:empty {
		// 	display: none;
		// 	visibility: hidden;
		// }
	}
	&__link-info {
		&:empty {
			display: none;
			visibility: hidden;
		}
	}
}
.footer-rights {
	display: block;
	text-align: center;
	padding: 10px;
	font-size: 12px;
	&__row {
		display: inline-flex;
		justify-content: center;
		align-items: center;
		gap: 5px;
	}
	&__logo {
		display: inline-flex;
		justify-content: center;
		align-items: center;
		text-transform: uppercase;
		gap: 5px;
	}
	&__img {
		display: inline-block;
		height: 16px;
	}
}
