.user-menu {
	--action-area-bg: var(--bg-color);
	--nav-bg: var(--white-dark);
	position: fixed;
	top: 0;
	right: 0;
	display: grid;
	grid-template-rows: 50px 1fr 70px;
	max-width: 360px;
	width: 100vw;
	height: 100vh;
	z-index: 9;
	pointer-events: none;
	transform: translateX(100%);
	transition: transform 0.3s ease-in-out;
	&--active {
		pointer-events: all;
		transform: translateX(0);
	}
	&__action-area {
		display: flex;
		align-items: center;
		gap: 10px;
		padding: 0 10px;
		background: var(--action-area-bg);
	}
	&__deposit-link {
		flex: 1;
	}
	&__nav {
		display: block;
		padding: 20px 10px 30px;
		background: var(--nav-bg);
		overflow-y: auto;
		@media screen and (min-width: 360px) {
			padding: 20px 20px 30px;
		}
	}
	&__row {
		display: flex;
		gap: 10px;
		margin-bottom: 10px;
		@media screen and (min-width: 360px) {
			gap: 20px;
		}
	}
	&__list {
		flex: 1;
	}
	&__item {
		position: relative;
		margin-bottom: 10px;
		&--sub-menu {
			&::after {
				content: "";
				display: block;
				position: absolute;
				top: 20px;
				right: 16px;
				width: 0;
				height: 0;
				border-style: solid;
				border-width: 4px 4px 0 4px;
				border-color: var(--font-color) transparent transparent
					transparent;
				pointer-events: none;
				transition: transform 0.2s ease-in-out;
			}
		}
		&--active {
			&::after {
				transform: rotate(180deg);
			}
		}
	}
	&__footer {
		display: flex;
		justify-content: center;
		align-items: center;
		padding: 0 10px;
		width: 100%;
		height: 70px;
		background: var(--action-area-bg);
	}
}
