.win-now {
	--min-width: 160px;
	--item-bg: var(--white-010);
	display: block;
	overflow: hidden;
	&__row {
		display: flex;
		gap: 10px;
		mask-image: linear-gradient(
			90deg,
			#fff,
			#fff 3%,
			#fff 97%,
			hsla(0deg, 0%, 100%, 0)
		);
	}
	&__item {
		display: block;
		padding: 10px;
		width: var(--min-width);
		min-width: var(--min-width);
		background: var(--item-bg);
		font-size: 12px;
		font-weight: 700;
		text-align: center;
		text-shadow: 0 1px 1px rgba(0, 0, 0, 0.6);
	}
	&__player {
		display: flex;
		justify-content: center;
		align-items: center;
		height: 25px;
		color: var(--decor-color);
	}
	&__value {
		--win-value-font-color: var(--font-color);
		--win-value-bg-color: var(--decor-color);
		--win-value-shadow-top: var(--yellow-light);
		--win-value-shadow-bottom: var(--yellow-dark);
		display: inline-flex;
		justify-content: center;
		align-items: center;
		padding: 0 8px;
		width: auto;
		height: 24px;
		font-size: 14px;
		font-weight: 700;
		color: var(--win-value-font-color);
		background: var(--win-value-bg-color);
	}
	&__game-link {
		display: flex;
		justify-content: center;
		align-items: center;
		width: auto;
		height: 25px;
		color: var(--white);
		span {
			display: inline-block;
			&::first-letter {
				text-transform: capitalize;
			}
		}
		&:hover {
			color: var(--decor-color);
		}
	}
	.game-item {
		&__title,
		&__btn-row {
			display: none;
			visibility: hidden;
		}
	}
	@media screen and (min-width: 1200px) {
		--min-width: 200px;
	}
}
