.header {
	position: relative;
	// padding-bottom: var(--header-height);
	&__fixed {
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		height: var(--header-height);
		z-index: 8;
		transition: transform 0.3s ease-in-out;
		&--hide {
			transform: translateY(-120%);
		}
	}
	&__top {
		// --header-top-bg: var(--bg-color);
		position: relative;
		display: block;
		width: 100%;
		height: 50px;
	}
	&__row {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 0 10px;
		height: 50px;
	}
	&__item {
		flex-basis: 50%;
		display: flex;
		align-items: center;
		gap: 10px;
		&--left {
			justify-content: flex-start;
			padding-right: 60px;
			padding-left: 40px;
		}
		&--right {
			justify-content: flex-end;
			padding-left: 60px;
		}
	}
	&__center {
		--header-center-bg: var(--white-dark);
		position: relative;
		display: flex;
		align-items: flex-end;
		width: 100%;
		height: 40px;
		background: var(--header-center-bg);
		// z-index: 2;
	}
	&__deposit-link {
		display: none;
		visibility: hidden;
	}
	.lang,
	.action-btn.search-widget-trigger {
		display: none;
		visibility: hidden;
	}
	.registration-link {
		display: none;
		visibility: hidden;
	}
	.user {
		&__short-info {
			margin-top: 0;
			padding: 0;
		}
		&__value-side {
			display: none;
		}
		&__img-box,
		&__img {
			width: 40px;
			min-width: 40px;
			height: 40px;
			cursor: pointer;
		}
	}
	@media screen and (min-width: 500px) {
		.user {
			&__short-info {
				margin-right: -15px;
				padding-right: 15px;
			}
			&__value-side {
				display: block;
			}
		}
		.action-btn.search-widget-trigger {
			display: flex;
			visibility: visible;
		}
	}
	@media screen and (min-width: 600px) {
		&__deposit-link {
			display: inline-flex;
			visibility: visible;
		}
		.lang {
			display: flex;
			visibility: visible;
		}
		.search-area {
			display: flex;
			visibility: visible;
			margin-left: 10px;
		}
		.registration-link {
			display: inline-flex;
			visibility: visible;
		}
	}
	@media screen and (min-width: 1200px) {
		display: none;
		visibility: hidden;
	}
}

.hamburger {
	--hamburger-bg: var(--decor-color);
	--line-bg: var(--white);
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	width: 40px;
	min-width: 40px;
	height: 40px;
	border-radius: 0 50% 50% 0;
	background: var(--hamburger-bg);
	box-shadow: inset -1px -1px 0 0 var(--blue),
		inset 1px 1px 0 0 var(--purple-light);
	cursor: pointer;
	&--active {
		.hamburger {
			&__line {
				&--top {
					transform: translateX(3px) rotate(45deg);
				}
				&--center {
					transform: scaleX(0);
				}
				&--bottom {
					transform: translate(1px, 1px) rotate(-45deg);
				}
			}
		}
	}
	&__line {
		display: block;
		width: 16px;
		height: 2px;
		background: var(--line-bg);
		transition: transform 0.2s ease-in-out;
		&--top {
			transform-origin: top left;
		}
		&--center {
			margin: 3px 0;
		}
		&--bottom {
			transform-origin: top left;
		}
	}
}

.logo {
	--logo-font-color: var(--white-dark);
	--logo-bg: var(--bg-color);
