.search-area {
	--search-bg: var(--black-080);
	--search-active-color: var(--gray);
	--search-input-color: var(--font-color);
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	max-width: 220px;
	width: 100%;
	height: 30px;
	background: var(--search-bg);
	border: 2px solid var(--search-active-color);
	&:focus-within {
		--search-active-color: var(--decor-color);
	}
	input.search-area__input {
		display: block;
		padding-right: 30px;
		width: 100%;
		height: 100%;
		font-style: 17px;
		font-weight: 700;
		color: var(--search-input-color);
		background: transparent;
		border: none;
		border-radius: inherit;
		&:focus {
			&::-webkit-input-placeholder {
				/* Chrome/Opera/Safari */
				opacity: 0.6;
			}
			&::-moz-placeholder {
				/* Firefox 19+ */
				opacity: 0.6;
			}
			&:-ms-input-placeholder {
				/* IE 10+ */
				opacity: 0.6;
			}
			&:-moz-placeholder {
				/* Firefox 18- */
				opacity: 0.6;
			}
		}
		&::-webkit-input-placeholder {
			/* Chrome/Opera/Safari */
			color: inherit;
		}
		&::-moz-placeholder {
			/* Firefox 19+ */
			color: inherit;
		}
		&:-ms-input-placeholder {
			/* IE 10+ */
			color: inherit;
		}
		&:-moz-placeholder {
			/* Firefox 18- */
			color: inherit;
		}
	}
	&__img {
		position: absolute;
		top: 50%;
		right: 6px;
		transform: translateY(-50%);
		display: block;
		width: 18px;
		height: 18px;
		z-index: 2;
	}
}

.registration {
	&__row {
		display: flex;
		justify-content: center;
		align-items: center;
		gap: 10px;
		&--column {
			flex-direction: column;
		}
	}
}
.lang {
	--lang-bg: var(--bg-color);
	--lang-current-bg: var(--white-010);
	
	--lang-list-bg: var(--white);
	--lang-font-color: var(--font-color);
	--lang-link-current-bg: var(--white-010);
	position: relative;
	display: inline-flex;
	justify-content: center;
	align-items: center;
	&::after {
		content: "";
		position: absolute;
		top: 50%;
		right: 6px;
		transform: translateY(-50%);
		display: block;
		width: 0;
		height: 0;
		border-style: solid;
		border-width: 4px 4px 0 4px;
		border-color: var(--white) transparent transparent transparent;
		transition: transform 0.2s ease-in-out;
		pointer-events: none;
		z-index: 2;
	}
	&--active {
		&::after {
			transform: translateY(-50%) rotate(180deg);
		}
		.lang__list {
			pointer-events: all;
			opacity: 1;
			transform: translateY(12px);
		}
	}
	&__current {
		display: flex;
		align-items: center;
		padding-left: 5px;
		padding-right: 20px;
		width: 50px;
		height: 30px;
		background: var(--lang-current-bg);
		cursor: pointer;
	}
	&__list {
		position: absolute;
		top: 100%;
		left: 0;
		display: flex;
		flex-direction: column;
		padding: 2px;
		width: 250px;
		// background: var(--lang-list-bg);

		background: rgba(18, 18, 18, 0.5);
		border-radius: 0;
		box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
		backdrop-filter: blur(10px);
		-webkit-backdrop-filter: blur(10px);
		border: 2px solid rgba(18, 18, 18, 0.31);


		transform: translateY(25px);
		z-index: 2;
		pointer-events: none;
		opacity: 0;
		transition: transform 0.2s ease-in-out, opacity 0.2s ease-in-out;
	}
	&__item {
		order: 2;
		margin-bottom: 3px;
		&--current {
			order: 1;
		}
	}
	&__link {
		display: flex;
		align-items: center;
		padding: 0 10px;
		width: 100%;
		height: 30px;
		font-size: 12px;
		font-weight: 700;
		color: var(--lang-font-color);
		text-transform: capitalize;
		border-left: 2px solid transparent;
		&:hover,
		&--current {
			background: var(--lang-link-current-bg);
		}
		&--current {
			border-left-color: var(--white);
		}
	}
	&__img {
		display: block;
		width: 24px;
		height: 18px;
	}
	&__info {
		padding: 0 10px;
	}
}
.login {
	&__row {
		display: flex;
		justify-content: center;
		align-items: center;
		gap: 10px;
		&--column {
			flex-direction: column;
			padding: 0 15px;
		}
	}
}
.user {
	--user-color: var(--white-dark);
	--user-decor-color: var(--decor-color);
	--percent-bg: var(--gray-dark);
	--short-info-bg: var(--white-010);
	--short-info-top-shadow: var(--decor-color-2);
	position: relative;
	display: block;
	max-width: 340px;
	width: 100%;
	color: var(--user-color);
	&__header {
		position: relative;
		display: flex;
		align-items: center;
		gap: 10px;
		width: 100%;
		z-index: 2;
	}
	&__short-info {
		flex: 1;
		display: inline-flex;
		justify-content: center;
		align-items: center;
		margin-top: -20px;
		padding: 10px 4px 4px;
		width: 100%;
		min-height: 30px;
		max-height: 50px;
		background: var(--short-info-bg);
		white-space: nowrap;
	}
	&__value-side {
		flex: 1;
		padding: 0 10px;
		font-weight: 700;
		text-align: right;
	}
	&__value-item {
		font-size: 14px;
		&--bonus {
			color: var(--decor-color);
			font-size: 12px;
			sup {
				font-size: 0.4em;
			}
		}
		sup {
			font-size: 0.4em;
		}
	}
	&__type-side {
		position: relative;
		padding: 0 10px;
		font-size: 10px;
		line-height: 16px;
		&::after {
			content: "";
			position: absolute;
			top: 0;
			left: 0;
			transform: translateX(-50%);
			display: block;
			width: 2px;
			height: 100%;
			background: var(--purple-light);
			border-left: 1px solid var(--blue);
		}
	}
	&__img-area {
		position: relative;
		display: flex;
		align-items: center;
	}
	&__img-box {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 50px;
		min-width: 50px;
		height: 50px;
		border-radius: 50%;
	}
	&__img {
		display: block;
		width: 50px;
		min-width: 50px;
		height: 50px;
		border-radius: 50%;
		object-fit: cover;
	}
	&__status-box {
		display: block;
		padding: 0 10px;
		max-width: 70px;
		width: 100%;
	}
	&__name {
		display: block;
		max-width: 70px;
		width: 100%;
		font-size: 14px;
		font-weight: 700;
		color: var(--user-color);
		text-transform: capitalize;
		overflow: hidden;
		text-overflow: ellipsis;
	}
	&__status {
		display: block;
		max-width: 70px;
		width: 100%;
		font-size: 10px;
		text-transform: capitalize;
		overflow: hidden;
		text-overflow: ellipsis;
	}
	&__progress {
		display: flex;
		align-items: center;
		gap: 4px;
		margin-top: 2px;
		font-size: 10px;
		color: var(--user-decor-color);
	}
	&__progress-percent {
		padding: 0 3px;
	}
	&__progress-box {
		flex: 1;
		display: block;
		height: 6px;
		background: repeating-linear-gradient(
			90deg,
			var(--percent-bg),
			var(--percent-bg) 8px,
			var(--bg-color) 8px,
			var(--bg-color) 10px
		);
	}
	&__progress-line {
		display: block;
		height: inherit;
		width: attr(data-width);
		background: repeating-linear-gradient(
			90deg,
			var(--user-decor-color),
			var(--user-decor-color) 8px,
			var(--bg-color) 8px,
			var(--bg-color) 10px
		);
	}
	&__alert {
		position: absolute;
		bottom: 100%;
		left: -6px;
		transform: translateY(100%);
		display: flex;
		justify-content: center;
		align-items: center;
		width: 16px;
		height: 16px;
		font-size: 10px;
		line-height: 11px;
		font-weight: 700;
		color: var(--font-color);
		background-color: var(--red);
		border-radius: 50%;
		z-index: 3;
	}
	@media screen and (min-width: 360px) {
		&__status-box {
			max-width: 90px;
		}
	}
}
