.payment-system {
	display: block;
	&__slider {
		display: none;
		visibility: hidden;
		&.slick-initialized {
			display: block;
			visibility: visible;
		}
		.slick-list {
			padding: 0 40px;
			mask-image: linear-gradient(
				90deg,
				hsla(0, 0%, 100%, 0),
				#fff 3%,
				#fff 97%,
				hsla(0, 0%, 100%, 0)
			);
			@media screen and (min-width: 1300px) {
				mask-image: linear-gradient(
					90deg,
					hsla(0, 0%, 100%, 0),
					#fff 3%,
					#fff 97%,
					hsla(0, 0%, 100%, 0)
				);
			}
			@media screen and (min-width: 1600px) {
				mask-image: linear-gradient(
					90deg,
					hsla(0, 0%, 100%, 0),
					#fff 5%,
					#fff 95%,
					hsla(0, 0%, 100%, 0)
				);
			}
		}
	}
	&__item {
		display: flex;
		justify-content: center;
		align-items: center;
		// width: 100%;
		padding: 5px;
	}
	&__link {
		display: inline-flex;
		justify-content: center;
		align-items: center;
	}
	&__img-box {
		display: inline-flex;
		justify-content: center;
		align-items: center;
	}
	&__img {
		display: block;
		height: 40px;
	}
}
