.small-btn {
	--btn-font-color: var(--font-color);
	--btn-bg-color: var(--decor-color);
	--btn-border-color: var(--blue-light);
	position: relative;
	display: inline-flex;
	justify-content: center;
	align-items: center;
	padding: 0 15px;
	width: auto;
	height: 30px;
	font-size: 12px;
	font-weight: 700;
	color: var(--btn-font-color);
	background: var(--btn-bg-color);
	border: 1px solid var(--btn-border-color);
	cursor: pointer;
	&__info,
	span {
		display: inline-block;
		text-shadow: none;
		&::first-letter {
			text-transform: capitalize;
		}
	}
	&::before,
	&::after {
		content: "";
		display: block;
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		transform: scaleX(0);
		transition: transform 0.2s ease-in-out;
		pointer-events: none;
	}
	&::before {
		border: 3px solid var(--btn-border-color);
		border-bottom: none;
		border-right: none;
		transform-origin: top left;
	}
	&::after {
		border: 3px solid var(--btn-border-color);
		border-top: none;
		border-left: none;
		transform-origin: bottom right;
	}
	&:hover {
		box-shadow: 0px 0px 8px 3px var(--btn-bg-color);
		&::before,
		&::after {
			transform: scaleX(1);
		}
	}
	&--yellow {
		--btn-font-color: var(--black);
		--btn-bg-color: var(--yellow-dark);
		--btn-border-color: var(--yellow);
	}
	&--black {
		--btn-font-color: var(--white-dark);
		--btn-bg-color: var(--gray-dark);
		--btn-border-color: var(--gray-semi);
	}
	&--transparent {
		--btn-font-color: var(--font-color);
		--btn-bg-color: transparent;
		--btn-border-color: var(--gray);
	}
	&--small {
		padding: 0;
		max-width: 30px;
		width: 30px;
		min-width: 30px;
	}
	&--large {
		display: flex;
		justify-content: space-between;
		width: 100%;
	}
	&__img {
		display: block;
		width: 18px;
		height: 18px;
	}
	&--active {
		.small-btn-st0 {
			fill: var(--decor-color);
		}
	}
}

.btn {
	--btn-font-color: var(--white-dark);
	--btn-bg-color: var(--decor-color);
	--border-color: var(--blue-light);
	// --btn-shadow-top: var(--yellow-light);
	// --btn-shadow-bottom: var(--yellow-dark);
	position: relative;
	display: inline-flex;
	justify-content: center;
	align-items: center;
	padding: 0 15px;
	width: auto;
	min-width: 200px;
	height: 40px;
	font-size: 14px;
	font-weight: 700;
	color: var(--btn-font-color);
	text-transform: uppercase;
	background: var(--btn-bg-color);
	border: 1px solid var(--border-color);
	cursor: pointer;
	&::before,
	&::after {
		content: "";
		display: block;
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		transform: scaleX(0);
		transition: transform 0.2s ease-in-out;
		pointer-events: none;
	}
	&::before {
		border: 4px solid var(--border-color);
		border-bottom: none;
		border-right: none;
		transform-origin: top left;
	}
	&::after {
		border: 4px solid var(--border-color);
		border-top: none;
		border-left: none;
		transform-origin: bottom right;
	}
	&:hover {
		box-shadow: 0px 0px 8px 3px var(--btn-bg-color);
		&::before,
		&::after {
			transform: scaleX(1);
		}
	}
	&--yellow {
		--btn-font-color: var(--black);
		--btn-bg-color: var(--yellow-dark);
		--border-color: var(--yellow);
	}
	&--black {
		--btn-font-color: var(--white-dark);
		--btn-bg-color: var(--bg-color);
		--border-color: var(--gray-semi);
	}
	&--medium {
		min-width: 160px;
	}
	&--small {
		max-width: 90px;
		min-width: 90px;
	}
	&--play {
		max-width: 60px;
		min-width: 60px;
		height: 60px;
		border-radius: 50%;
		&::before,
		&::after {
			border-radius: 50%;
			transform-origin: center;
			border: 4px solid var(--border-color);
			transform: scale(0);
		}
		&:hover {
			&::before,
			&::after {
				transform: scale(1);
			}
		}
	}
	&.btn-chat {
		display: none;
		visibility: hidden;
	}
	&__triangle {
		display: block;
		width: 0;
		height: 0;
		border-style: solid;
		border-width: 15px 0 15px 20px;
		border-color: transparent transparent transparent var(--font-color);
		transform: translateX(20%);
	}
	&__img {
		display: block;
		margin-right: 10px;
		width: 30px;
		height: 30px;
	}
	input[type="submit"] {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		opacity: 0;
		z-index: 2;
	}
	&__info,
	span {
		text-shadow: none;
	}
	@media screen and (min-width: 600px) {
		&.btn-chat {
			display: inline-flex;
			visibility: visible;
			position: fixed;
			bottom: 20px;
			right: 30px;
			min-width: 140px;
			height: 50px;
			z-index: 8;
		}
	}
	@media screen and (min-width: 1921px) {
		&.btn-chat {
			right: calc(50vw - 930px);
		}
	}
}

.action-btn {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 35px;
	min-width: 35px;
	height: 35px;
	border-radius: 50%;
	cursor: pointer;
	&--active,
	&:hover {
		background-color: rgba($decor-color, 1);
		.action-btn__img {
			.action-btn__st0 {
				fill: var(--white);
			}
		}
	}
	&__img {
		display: block;
		width: 20px;
		height: 20px;
	}
}

.nav-link {
	--nav-link-color: var(--gray);
	--nav-link-bg: transparent;
	--nav-link-bg-hover: var(--white-005);
	position: relative;
	display: flex;
	align-items: center;
	gap: 5px;
	padding: 0 20px;
	width: auto;
	min-width: 100px;
	font-size: 13px;
	font-weight: 700;
	color: var(--nav-link-color);
	background: var(--nav-link-bg);
	border-left: 2px solid transparent;

	&:hover {
		--nav-link-bg: var(--nav-link-bg-hover);
	}

	&--current {
		--nav-link-color: var(--white-dark);
		--nav-link-bg: var(--white-010);
		border-left-color: var(--white-dark);
	}
	&__img {
		display: block;
		width: 40px;
		height: 40px;
	}
	&__info {
		display: inline-block;
		white-space: nowrap;
		&::first-letter {
			text-transform: capitalize;
		}
	}
	&__alert {
		position: absolute;
		top: -5px;
		right: -5px;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 16px;
		height: 16px;
		font-size: 10px;
		font-weight: 700;
		color: var(--white);
		border-radius: 50%;
		background: var(--red);
		z-index: 2;
		&:empty {
			display: none;
			visibility: hidden;
		}
	}
}

.btn-close {
	--btn-font-color: var(--white-dark);
	--btn-bg-color: var(--decor-color);
	--btn-border-color: var(--blue-light);
	position: relative;
	display: inline-flex;
	justify-content: center;
	align-items: center;
	width: 30px;
	height: 30px;
	background: var(--btn-bg-color);
	border: 1px solid var(--btn-border-color);
	transition: box-shadow 0.4s ease-in-out;
	cursor: pointer;
	&:hover {
		--btn-font-color: var(--white);
		--btn-bg-color: var(--gray-dark);
	}
	&__img {
		display: block;
		width: 30px;
		height: 30px;
	}
}

.btn-row {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-wrap: wrap;
	gap: 20px;
	padding: 20px 0;
}
