.mobile-menu {
	--action-area-bg: var(--bg-color);
	--nav-bg: var(--white-dark);
	position: fixed;
	top: 0;
	left: 0;
	display: block;
	z-index: 9;
	transform: translateX(-100%);
	transition: transform 0.3s ease-in-out;
	text-shadow: 0 1px 1px rgba(0, 0, 0, 0.6);
	&--active {
		pointer-events: all;
		transform: translateX(0);
	}
	&__body {
		position: relative;
		display: grid;
		grid-template-rows: auto 1fr;
		padding: 2px;
		max-width: 270px;
		width: 100vw;
		height: 100vh;
		&::before {
			content: "";
			display: block;
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background: rgba(18, 18, 18, 0.5);
			border-radius: 0;
			box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
			backdrop-filter: blur(10px);
			-webkit-backdrop-filter: blur(10px);
			border: 2px solid rgba(18, 18, 18, 0.81);
			z-index: 1;
		}
		& > * {
			position: relative;
			z-index: 2;
		}
	}
	&__action-area {
		position: relative;
		display: flex;
		align-items: center;
		gap: 10px;
		padding: 10px 15px;
		// background: var(--action-area-bg);
		&--column {
			justify-content: center;
			flex-direction: column;
		}
	}
	&__hamburger {
		position: absolute;
		top: 3px;
		right: -42px;
	}
	&__log-reg-area {
		display: block;
		padding: 15px 0;
		width: 100%;
	}
	&__content {
		padding-bottom: 60px;
		overflow-y: auto;
	}
	&__nav {
		display: block;
	}
	&__row {
		display: flex;
		gap: 10px;
		margin-bottom: 10px;
		@media screen and (min-width: 360px) {
			gap: 20px;
		}
	}
	&__list {
		flex: 1;
		padding: 15px 0;
	}
	&__item {
		position: relative;
		&--sub-menu {
			&::after {
				content: "";
				display: block;
				position: absolute;
				top: 20px;
				right: 16px;
				width: 0;
				height: 0;
				border-style: solid;
				border-width: 4px 4px 0 4px;
				border-color: var(--white-dark) transparent transparent
					transparent;
				pointer-events: none;
				transition: transform 0.2s ease-in-out;
			}
		}
		&--active {
			&::after {
				transform: rotate(180deg);
			}
			& > .nav-link {
				--nav-link-bg: var(--nav-link-bg-hover);
			}
		}
	}
	.registration-link {
		display: none;
		visibility: hidden;
	}
	@media screen and (min-width: 360px) {
		.registration-link {
			display: inline-flex;
			visibility: visible;
		}
	}
	@media screen and (min-width: 1200px) {
		pointer-events: all;
		transform: translateX(0);
		&.mobile-menu--active {
			.mobile-menu {
				&__body {
					max-width: 270px;
				}
				&__action-area {
					padding: 10px 15px;
				}
				&__item--sub-menu::after {
					right: 16px;
				}
			}
			.logo {
				&__img {
					display: block;
					visibility: visible;
				}
				&__mini-img {
					display: none;
					visibility: hidden;
				}
			}
			.search-widget-trigger {
				justify-content: space-between;
				.small-btn__info {
					display: block;
					visibility: visible;
				}
			}
			.mobile-lang {
				&::after {
					right: 16px;
				}
				&__link {
					justify-content: flex-start;
				}
				&__info {
					display: block;
					visibility: visible;
				}
			}
			.login {
				&__row--column {
					padding: 0 15px;
				}
			}
			.user {
				&__header {
					justify-content: flex-start;
				}
				&__alert {
					left: -6px;
				}
				&__name {
					display: block;
					visibility: visible;
				}
				&__short-info {
					display: inline-flex;
					visibility: visible;
				}
				&__progress {
					display: flex;
					visibility: visible;
				}
			}
			.btn {
				display: inline-flex;
				visibility: visible;
			}
			.nav-link {
				justify-content: flex-start;
				padding: 0 20px;
				&__info {
					display: inline-block;
					visibility: visible;
				}
			}
		}
		&__hamburger {
			left: -2px;
			right: unset;
		}
		&__body {
			max-width: 80px;
		}
		&__action-area {
			padding: 50px 3px 10px;
		}
		&__item--sub-menu::after {
			right: 6px;
		}
		.logo {
			padding: 0;
			&__img {
				display: none;
				visibility: hidden;
			}
			&__mini-img {
				display: block;
				visibility: visible;
			}
		}
		.search-widget-trigger {
			justify-content: center;
			.small-btn__info {
				display: none;
				visibility: hidden;
			}
		}
		.mobile-lang {
			&::after {
				right: 5px;
			}
			&__link {
				justify-content: center;
			}
			&__info {
				display: none;
				visibility: hidden;
			}
		}
		.login {
			&__row--column {
				padding: 0;
			}
		}
		.user {
			&__header {
				justify-content: center;
			}
			&__alert {
				left: 0;
			}
			&__name {
				display: none;
				visibility: hidden;
			}
			&__short-info {
				display: none;
				visibility: hidden;
			}
			&__progress {
				display: none;
				visibility: hidden;
			}
		}
		.btn {
			display: none;
			visibility: hidden;
		}
		.nav-link {
			justify-content: center;
			padding: 0;
			min-width: 60px;
			&__info {
				display: none;
				visibility: hidden;
			}
		}
	}
	@media screen and (min-width: 1921px) {
		left: calc(50vw - 960px);
	}
}
.sub-menu {
	--sub-menu-bg: var(--white-010);
	--sub-menu-link-hover-bg: var(--white-005);
	--sub-menu-link-color: var(--gray);
	display: none;
	visibility: hidden;
	padding: 10px;
	background: var(--sub-menu-bg);
	&--active {
		display: block;
		visibility: visible;
	}
	&__item {
	}
	&__link {
		position: relative;
		display: flex;
		align-items: center;
		padding-left: 40px;
		padding-right: 10px;
		width: 100%;
		height: 40px;
		color: var(--sub-menu-link-color);
		font-weight: 700;
		border-left: 2px solid transparent;
		cursor: pointer;
		&--current {
			--sub-menu-link-color: var(--white-dark);
			border-left-color: var(--white-dark);
		}
		&--current,
		&:hover {
			background: var(--sub-menu-link-hover-bg);
		}
	}
	&__link-info {
		display: inline-block;
		&::first-letter {
			text-transform: capitalize;
		}
	}
}
.mobile-lang {
	--mobile-lang-bg: var(--white-010);
	--lang-font-color: var(--font-color);
	--lang-link-current-bg: var(--white-005);
	position: relative;
	display: flex;
	flex-direction: column;
	height: 40px;
	background: var(--mobile-lang-bg);
	// border-radius: 10px;
	overflow: hidden;
	&::after {
		content: "";
		display: block;
		position: absolute;
		top: 20px;
		right: 16px;
		width: 0;
		height: 0;
		border-style: solid;
		border-width: 4px 4px 0 4px;
		border-color: var(--font-color) transparent transparent transparent;
		pointer-events: none;
		transition: transform 0.2s ease-in-out;
	}
	&--active {
		height: auto;
		&::after {
			transform: rotate(180deg);
		}
		.mobile-lang__link {
			&--current {
				background: var(--lang-link-current-bg);
			}
		}
	}
	&__item {
		order: 2;
		margin-bottom: 3px;
		&--current {
			order: 1;
		}
		&:last-child {
			margin-bottom: 0;
		}
	}
	&__link {
		display: flex;
		align-items: center;
		padding: 0 10px;
		width: 100%;
		height: 40px;
		font-size: 12px;
		font-weight: 700;
		color: var(--lang-font-color);
		text-transform: capitalize;
		// border-radius: 10px;
		cursor: pointer;
		&:hover {
			background: var(--lang-link-current-bg);
		}
	}
	&__img {
		display: block;
		width: 24px;
		height: 18px;
	}
	&__info {
		padding: 0 7px;
	}
}
