*,
*:before,
*:after {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	font-family: "Montserrat", sans-serif;
}
/* width / height */
::-webkit-scrollbar {
	width: 16px;
	height: 16px;
}

/* Track */
::-webkit-scrollbar-track {
	background: transparent;
	width: 16px;
	height: 16px;
}

/* Handle */
::-webkit-scrollbar-thumb {
	// background: var(--decor-color);
	background: rgba($decor-color, 0.6);
	width: 16px;
	height: 16px;
	&:hover {
		background: rgba($decor-color, 1);
	}
}
body {
	position: relative;
	padding-top: var(--header-height);
	font-size: 12px;
	font-weight: 400;
	color: var(--font-color);
	background-color: var(--bg-color);
	background-image: repeating-radial-gradient(
			circle at 0 0,
			transparent 0,
			var(--bg-color) 40px
		),
		repeating-linear-gradient(#185cc155, #185cc1);
	background-position: center center;
	background-size: cover;
	background-repeat: no-repeat;
	background-attachment: fixed;
	overflow-x: hidden;
	&.on-line {
		.registration {
			display: none;
			visibility: hidden;
		}
		.login {
			display: block;
			visibility: visible;
		}
		.action-area {
			display: none;
			visibility: hidden;
		}
	}
	.registration {
		display: block;
		visibility: visible;
	}
	.login {
		display: none;
		visibility: hidden;
	}
	@media screen and (min-width: 1200px) {
		padding-top: 0;
	}
	@media screen and (min-width: 1600px) {
		font-size: 14px;
	}
}

.dn {
	display: none !important;
	visibility: hidden !important;
}

.decor-color {
	color: var(--decor-color);
}
.decor-bg {
	background: var(--decor-color);
}

h1,
.h1 {
	font-size: 24px;
	@media screen and (min-width: 1600px) {
		font-size: 30px;
	}
}
h2,
.h2 {
	font-size: 14px;
	font-weight: 700;
	color: inherit;
	text-transform: uppercase;
	@media screen and (min-width: 360px) {
		font-size: 16px;
	}
}
h3,
.h3 {
	font-size: 14px;
}
h4,
.h4 {
	font-size: 12px;
}

b,
strong,
.bold {
	font-weight: 700;
}

.text-shadow {
	text-shadow: 0 1px 1px rgba(0, 0, 0, 0.6);
}

.overlay {
	--overlay-bg: var(--black);
	position: fixed;
	top: 0;
	left: 0;
	width: 100vw;
	height: 100vh;
	background: var(--overlay-bg);
	opacity: 0;
	z-index: 9;
	transform: translateY(-40%);
	transition: transform 0.3s ease-in-out, opacity 0.3s ease-in-out;
	pointer-events: none;
	&--active {
		pointer-events: all;
		opacity: 0.8;
		transform: translateY(0);
	}
}

.wrapper {
	display: block;
	margin: 0 auto;
	padding: 0 10px;
	max-width: 1920px;
	width: 100%;
}

main {
	min-height: 100vh;
}

// .slick-slider {
// }
.slick-prev,
.slick-next {
	--arrow-color: var(--white-020);
	--triangle-color: var(--white);
	display: flex;
	justify-content: center;
	align-items: center;
	width: 40px;
	height: 30px;
	background: var(--arrow-color);
	box-shadow: 0px 4px 10px 0px rgba(21, 21, 21, 0.1);
	z-index: 3;
	&::before {
		content: "";
		width: 0;
		height: 0;
		border-style: solid;
		opacity: 1;
	}
	&:focus,
	&:hover {
		background: var(--arrow-color);
	}
}
.slick-prev {
	left: 10px;
	&::before {
		border-width: 6px 6px 6px 0;
		border-color: transparent var(--triangle-color) transparent transparent;
		transform: translateX(50%);
	}
}
.slick-next {
	right: 10px;
	&::before {
		border-width: 6px 0 6px 6px;
		border-color: transparent transparent transparent var(--triangle-color);
		transform: translateX(50%);
	}
}
