.content-grid {
	position: relative;
	min-height: inherit;
	overflow: hidden;
	&::before {
		content: "";
		display: block;
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: rgba(18, 18, 18, 0.5);
		border-radius: 0;
		box-shadow: 0 4px 30px rgb(0 0 0 / 10%);
		backdrop-filter: blur(10px);
		-webkit-backdrop-filter: blur(10px);
		border: 2px solid rgba(18, 18, 18, 0.31);
		z-index: 1;
	}
	& > * {
		position: relative;
		z-index: 2;
	}
}

