.heading {
	--heading-color: var(--white);
	--heading-bg: var(--white-020);
	display: flex;
	// justify-content: center;
	align-items: center;
	gap: 10px;
	margin-bottom: 15px;
	padding-bottom: 15px;
	border-bottom: 2px solid var(--heading-bg);
	&--center {
		justify-content: center;
	}
	&--slider {
		padding-left: 90px;
	}
	&__line {
		flex: 1;
		display: block;
		width: 100%;
		height: 2px;
		background: var(--heading-bg);
	}
	&__content {
		text-shadow: 0 1px 1px rgb(0 0 0 / 60%);
		& * {
			margin-bottom: 0;
		}
	}
	&__link {
		position: relative;
		display: inline-flex;
		justify-content: center;
		align-items: center;
		padding-right: 17px;
		font-size: 16px;
		font-weight: 700;
		color: var(--heading-color);
		text-transform: uppercase;
		&::after {
			content: "";
			position: absolute;
			top: 50%;
			right: 0;
			transform: translateY(-50%);
			display: block;
			width: 0;
			height: 0;
			border-style: solid;
			border-width: 6px 0 6px 6px;
			border-color: transparent transparent transparent
				var(--heading-color);
		}
		&:hover {
			--heading-color: var(--decor-color);
		}
	}
	@media screen and (min-width: 390px) {
		&--slider {
			padding-left: 110px;
		}
	}
}
