.shape {
	position: relative;
	// background: var(--white-010);
	&::before {
		content: "";
		display: block;
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: rgba(18, 18, 18, 0.5);
		border-radius: 0;
		box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
		backdrop-filter: blur(10px);
		-webkit-backdrop-filter: blur(10px);
		border: 2px solid rgba(18, 18, 18, 0.31);
		z-index: 1;
	}
	&__close {
		position: absolute;
		top: 0;
		right: 0;
		z-index: 3;
	}
	&__body {
		position: relative;
		z-index: 2;
	}
}

.tab {
	display: block;
	width: 100%;
	&__menu {
		display: flex;
		justify-content: space-between;
		align-items: center;
		gap: 5px;
		margin-bottom: 15px;
		padding: 0 10px;
		&--center {
			justify-content: center;
			flex-wrap: wrap;
			// gap: 10px 20px;
			.tab {
				&__menu-item {
					flex-grow: 1;
				}
				&__menu-btn {
					width: 100%;
				}
			}
		}
	}
	&__menu-item {
		&--active {
			.tab {
				&__step {
					color: var(--white);
					background: var(--decor-color);
					.tab__icon {
						fill: var(--white);
					}
				}
				&__menu-btn {
					--btn-font-color: var(--font-color);
					--btn-bg-color: var(--decor-color);
				}
			}
		}
	}
	&__menu-btn {
		--btn-font-color: var(--white);
		--btn-bg-color: var(--gray-dark);
		position: relative;
		display: inline-flex;
		justify-content: center;
		align-items: center;
		padding: 0 5px;
		width: auto;
		height: 30px;
		font-size: 10px;
		font-weight: 700;
		color: var(--btn-font-color);
		background: var(--btn-bg-color);
		// transition: box-shadow 0.4s ease-in-out;
		cursor: pointer;
		&:hover {
			--btn-bg-color: var(--decor-color);
		}
	}
	&__step {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 30px;
		min-width: 30px;
		height: 30px;
		font-size: 16px;
		font-weight: 700;
		color: var(--font-color);
		background: var(--gray-semi);
		cursor: pointer;
	}
	&__img {
		display: block;
		width: 20px;
		height: 20px;
	}
	&__icon {
		fill: var(--font-color);
	}
	&__menu-decor {
		flex: 1;
		display: block;
		height: 4px;
		border-bottom: 4px dashed var(--gray);
		pointer-events: none;
	}
	@media screen and (min-width: 360px) {
		&__menu-btn {
			font-size: 12px;
		}
	}
	@media screen and (min-width: 760px) {
		&__menu {
			&--center {
				gap: 10px 20px;
				.tab {
					&__menu-item {
						flex-grow: unset;
					}
					&__menu-btn {
						width: auto;
					}
				}
			}
		}
		&__menu-btn {
			padding: 0 15px;
			min-width: 160px;
			height: 60px;
			font-size: 14px;
			
			
			&:hover {
				
			}
		}
	}
	@media screen and (min-width: 900px) {
		&__menu-btn {
			min-width: 200px;
		}
	}
}

.bonus-page {
	&__item {
		padding: 30px 0 15px;
	}
	&__grid {
		--min-width: 300px;
		display: grid;
		justify-content: center;
		grid-template-columns: repeat(auto-fill, var(--min-width));
		gap: 10px;
		padding: 5px;
		@media screen and (min-width: 1300px) {
			--min-width: 360px;
		}
		@media screen and (min-width: 1900px) {
			gap: 20px;
			padding: 5px 10px;
		}
	}
}
.table-container {
	display: block;
	width: 100%;
	overflow-x: auto;
}
.table {
	--table-head-color: var(--white);
	--table-color: var(--font-color);
	--table-bg: var(--white-010);
	// --table-shadow: var(--white);
	border-collapse: collapse;
	width: 100%;
	thead {
		tr {
			td,
			th {
				color: var(--table-head-color);
				background: var(--decor-color);
			}
		}
	}
	tr {
		&:nth-child(odd) {
			background: var(--table-bg);
		}
		td,
		th {
			padding: 5px 10px;
			white-space: nowrap;
			color: var(--table-color);
			border-bottom: 1px solid transparent;

			&.decor-bg {
				background: var(--decor-color);
				border-bottom: 1px solid var(--white-020);
			}
		}
	}
	a {
		color: var(--decor-color-2);
		text-decoration: underline;
		&:hover {
			text-decoration: none;
		}
	}
	&__sort {
		position: relative;
		display: inline-block;
		padding-right: 15px;
		cursor: pointer;
		text-transform: lowercase;
		&::first-letter {
			text-transform: uppercase;
		}
		&::before,
		&::after {
			content: "";
			display: block;
			position: absolute;
			right: 0;
			top: 50%;
			width: 0;
			height: 0;
			border-style: solid;
		}
		&::before {
			border-width: 0 4px 4px 4px;
			border-color: transparent transparent var(--white-dark) transparent;
			-webkit-transform: translateY(-125%);
			-ms-transform: translateY(-125%);
			transform: translateY(-125%);
		}
		&::after {
			border-width: 4px 4px 0 4px;
			border-color: var(--white-dark) transparent transparent transparent;
			-webkit-transform: translateY(25%);
			-ms-transform: translateY(25%);
			transform: translateY(25%);
		}
	}
	&__ok {
		--table-color: var(--green);
	}
	&__cancel {
		--table-color: var(--gray);
		opacity: 0.6;
	}
	&__reject {
		--table-color: var(--red);

	}
	&__img-box {
		display: block;
		cursor: pointer;
	}
	&__img {
		display: block;
		width: 20px;
		height: 20px;
	}
}

.iframe-box {
	position: relative;
	display: block;
	min-width: 100%;
	min-height: 100vh;
	iframe {
		min-height: 100vh;
	}
}

ul.dot-list {
	padding-left: 10px;
	li {
		margin-bottom: 0;
		&::before {
			content: "";
			display: inline-block;
			margin-right: 5px;
			margin-bottom: 1px;
			width: 5px;
			height: 5px;
			background: var(--decor-color);
			border-radius: 50%;
		}
	}
}
