.account {
	text-shadow: 0 1px 1px rgba(0, 0, 0, 0.6);
	&__grid {
		display: grid;
		justify-items: center;
		gap: 34px;
		padding: 20px 0;
	}
	&__row {
		display: flex;
		justify-content: space-around;
		align-items: center;
		flex-wrap: wrap;
		gap: 10px;
	}
	&__bonus-shape,
	&__shape {
		position: relative;
		display: block;
		width: calc(100vw - 20px);
		max-width: 360px;
		padding: 10px 20px;
		color: var(--font-color);
		background: var(--white-010);
		&--large {
			max-width: 1236px;
		}
		&--blue {
			background: var(--decor-color);
		}
		// .heading {
		// 	--heading-bg: var(--white-dark);
		// }
	}
	&__messaage {
		font-size: 12px;
		line-height: 1.6667;
		a {
			font-weight: 700;
			color: var(--decor-color);
			text-decoration: underline;
			&:hover {
				text-decoration: none;
			}
		}
	}
	&__messaage-date {
		font-weight: 700;
	}
	&__img-box {
		position: relative;
		display: flex;
		justify-content: center;
		&--active {
			.account__img-gallery {
				transform: translate(-50%, 0);
				opacity: 1;
				pointer-events: all;
			}
		}
	}
	&__img-current,
	&__img-item {
		--shadow-color: var(--white-020);
		display: flex;
		justify-content: center;
		align-items: center;
		border-radius: 50%;
		overflow: hidden;
		box-shadow: 0 0 0 5px var(--shadow-color);
		cursor: pointer;
		&--current,
		&:hover {
			--shadow-color: var(--decor-color);
		}
	}
	&__img-current {
		width: 90px;
		min-width: 90px;
		height: 90px;
	}
	&__img-item {
		width: 80px;
		min-width: 80px;
		height: 80px;
	}
	&__img {
		display: block;
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
	&__img-gallery {
		position: absolute;
		top: -5px;
		left: 50%;
		transform: translate(-50%, 20%);
		box-shadow: 0 4px 10px 0 rgb(21 21 21 / 10%);
		border-radius: 25px;
		z-index: 2;
		pointer-events: none;
		opacity: 0;
		transition: transform 0.2s ease-in-out, opacity 0.2s ease-in-out;
	}
	&__img-list {
		display: flex;
		justify-content: center;
		flex-wrap: wrap;
		gap: 18px;
		margin: 0 -20px;
		padding: 5px 15px;
		max-height: 300px;
		overflow-x: hidden;
		overflow-y: auto;
	}
	&__img-gallery-close {
		position: absolute;
		top: -20px;
		right: -20px;
	}
	&__favorite-game {
		position: relative;
		display: block;
		max-width: 300px;
		width: 100%;
	}
	&__favorite-remove {
		position: absolute;
		top: -5px;
		right: -4px;
		z-index: 5;
	}
	&__filter-box {
		.input-area {
			margin-top: 0;
			max-width: 180px;
			&::after {
				content: none;
			}
		}
	}
	&__filter-row {
		display: flex;
		justify-content: space-between;
		align-items: center;
		flex-wrap: wrap;
		gap: 10px;
		padding-bottom: 10px;
		width: 100%;
	}
	&__filter-item {
		display: flex;
		justify-content: center;
		align-items: center;
		gap: 10px;
		width: 100%;
	}
	@media screen and (min-width: 760px) {
		&__filter-item {
			width: auto;
			.select--big {
				min-width: 180px;
			}
		}
	}
	@media screen and (min-width: 1100px) {
		&__filter-row {
			justify-content: flex-start;
		}
		&__filter-item {
			&:last-child {
				margin-left: auto;
			}
		}
	}
	@media screen and (min-width: 1200px) {
		&__shape {
			&--large {
				width: 100%;
			}
		}
	}
	@media screen and (min-width: 1600px) {
		&__shape {
			max-width: 556px;
			&--big {
				max-width: 1236px;
			}
			&--large {
				max-width: 1236px;
			}
		}
		&__bonus-shape {
			max-width: 390px;
		}
		&__form {
			padding: 0 70px;
		}
		&__form-row {
			display: flex;
			justify-content: center;
			align-items: flex-start;
			gap: 40px;
		}
		&__form-item {
			flex: 1;
			max-width: 380px;
		}
		&__messaage {
			font-size: 14px;
		}
	}
}

.account-menu {
	--link-color: var(--gray);
	position: relative;
	z-index: 2;
	display: block;
	width: 100%;
	&__row {
		display: flex;
		justify-content: center;
		width: 100%;
		border-bottom: 1px solid var(--white-020);
	}
	&__list {
		display: flex;
		justify-content: flex-start;
		align-items: flex-start;
		width: auto;
		text-align: center;
		overflow-y: hidden;
		overflow-x: auto;
	}
	&__item {
		position: relative;
		display: inline-flex;
		justify-content: center;
		align-items: center;
	}
	&__link {
		display: inline-flex;
		justify-content: center;
		align-items: center;
		gap: 10px;
		padding: 0 20px;
		height: 40px;
		text-shadow: 0 1px 1px rgb(0 0 0 / 60%);
		background: var(--black-050);
		border-bottom: 2px solid transparent;
		&:hover,
		&--active {
			--link-color: var(--white);
			background: var(--white-010);
		}
		&--active {
			border-bottom-color: var(--white);
		}
		&--sub-menu {
			.account-menu__link-info {
				position: relative;
				padding-right: 25px;
				&::after {
					content: "";
					display: block;
					position: absolute;
					top: 50%;
					right: 5px;
					transform: translateY(-25%);
					width: 0;
					height: 0;
					border-style: solid;
					border-width: 4px 4px 0 4px;
					border-color: var(--white) transparent transparent
						transparent;
				}
			}
			&:hover,
			&--active {
				.account-menu__link-info {
					padding-right: 25px;
				}
			}
		}
	}
	&__link-img-box {
		display: block;
		width: 40px;
		height: 40px;
	}
	&__link-img {
		display: block;
		width: 40px;
		height: 40px;
		object-fit: cover;
	}
	&__link-info {
		display: inline-block;
		font-size: 12px;
		font-weight: 700;
		color: var(--link-color);
		white-space: nowrap;
		text-transform: uppercase;
	}
	&__sub-menu {
		--sub-menu-bg: var(--black-dark);
		display: none;
		visibility: hidden;
		background: var(--sub-menu-bg);
		&--active {
			display: block;
			visibility: visible;
		}
	}
	@media screen and (min-width: 1200px) {
		&__link-info {
			font-size: 16px;
		}
	}
}
