.countdown {
	display: flex;
	justify-content: center;
	align-items: center;
	gap: 10px;
	margin-bottom: 15px;
	&__item {
		position: relative;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: flex-end;
		padding: 7px 0;
		width: 50px;
		height: 60px;
		background: linear-gradient(
			to bottom,
			rgba(255, 255, 255, 0.15) 0%,
			rgba(255, 255, 255, 0.15) 50%,
			rgba(255, 255, 255, 0.25) 50%,
			rgba(255, 255, 255, 0.25) 100%
		);
		border-radius: 8px;
		&::after {
			content: ":";
			position: absolute;
			top: 50%;
			right: 0;
			transform: translate(130%, -60%);
			font-size: 24px;
			font-weight: 700;
		}
		&:first-child,
		&:last-child {
			&::after {
				content: none;
			}
		}
	}
	&__count {
		font-size: 24px;
		line-height: 1;
		text-shadow: 0 1px 1px rgba(0, 0, 0, 0.6);
	}
	&__name {
		font-size: 10px;
		text-shadow: 0 1px 1px rgba(0, 0, 0, 0.6);
	}
}
