.testimonials {
	&__slider {
		display: none;
		visibility: hidden;
		&.slick-initialized {
			display: block;
			visibility: visible;
		}
		.slick-slide {
			padding: 0 3px;
		}
	}
	&__item {
		display: inline-block;
		width: 300px;
		overflow: hidden;
	}
	&__info-box {
		display: block;
		padding: 15px;
		width: 100%;
		height: 90px;
		color: var(--black);
		text-align: center;
		background: var(--white-dark);
		text-shadow: 0 1px 1px rgba(255, 255, 255, 0.9);
	}
	&__info {
		position: relative;
		display: -webkit-box;
		-webkit-line-clamp: 4;
		-webkit-box-orient: vertical;
		overflow: hidden;
		font-size: 12px;
		&::before {
			content: "";
		}
		&::after {
			content: "";
		}
	}
	&__author-box {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		width: 100%;
		height: 60px;
		font-size: 14px;
		color: var(--white-dark);
		background: var(--white-010);
	}
	&__img {
		display: block;
		width: auto;
		max-height: 40px;
		height: auto;
		filter: grayscale(1);
	}
	&__author-name {
		font-weight: 700;
		text-transform: uppercase;
	}
	&__author-country {
		display: inline-block;
		&::first-letter {
			text-transform: uppercase;
		}
	}
	@media screen and (min-width: 330px) {
		&__item {
			width: 310px;
		}
	}
	@media screen and (min-width: 350px) {
		&__slider {
			.slick-list {
				mask-image: linear-gradient(
					90deg,
					#fff,
					#fff 2%,
					#fff 97%,
					hsla(0deg, 0%, 100%, 0)
				);
			}
		}
	}
	@media screen and (min-width: 1900px) {
		&__slider {
			.slick-list {
				mask-image: none;
			}
		}
	}
}
