.popup {
	--popup-bg: var(--gray-dark);
	--popup-border: var(--gray-semi);
	position: fixed;
	top: 20px;
	left: 50%;
	display: inline-block;
	width: auto;
	z-index: 10;
	transform: translate(-50%, -40px);
	opacity: 0;
	pointer-events: none;
	transition: transform 0.2s ease-in-out, opacity 0.2s ease-in-out;
	&--active {
		transform: translate(-50%, 0);
		opacity: 1;
		pointer-events: all;
	}
	&__body {
		position: relative;
		display: inline-flex;
		padding: 15px 0;
		width: auto;
		height: auto;
		background: var(--popup-bg);
		border: 2px solid var(--popup-border);
	}
	&__close {
		position: absolute;
		top: 0;
		right: 0;
		z-index: 2;
	}
	&__content {
		padding-right: 15px;
		padding-left: 15px;
		min-width: 310px;
		max-height: calc(100vh - 140px);
		height: auto;
		font-size: 14px;
		// line-height: 16px;
		overflow-y: auto;
		// &::-webkit-scrollbar-track {
		// 	background: var(--white-dark);
		// }
		a {
			color: var(--decor-color-2);
			text-decoration: underline;
			&:hover {
				text-decoration: none;
			}
		}
	}
	@media screen and (min-width: 360px) {
		&__content {
			min-width: 350px;
		}
	}
	@media screen and (min-width: 440px) {
		// &__close {
		// 	right: -23px;
		// }
		&__content {
			min-width: 400px;
		}
	}
}
