.play-game {
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
	height: calc(100vh - var(--header-height));
	&--full-screen {
		height: 100vh;
		z-index: 10;
		.play-game {
			&__box {
				width: 100%;
				height: 100%;
			}
			&__header > .small-btn {
				transform: translateY(0);
			}
			&__action-box {
				transform: translate(0, 0);
			}
		}
	}
	&__img-bg {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		z-index: 1;
		pointer-events: none;
	}
	&__box {
		position: relative;
		display: block;
		width: calc((100vh - 150px) * 1.77);
		height: calc(100vh - 120px);
		z-index: 2;
	}
	&__header {
		display: flex;
		justify-content: space-between;
		align-items: center;
		gap: 10px;
		padding: 0 5px;
		height: 45px;
		background-color: var(--white-010);
	}
	&__title {
		display: flex;
		align-items: center;
		gap: 10px;
		font-size: 12px;
		font-weight: 700;
		color: var(--white-dark);
	}
	&__title-img {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 30px;
		min-width: 30px;
		height: 30px;
	}
	&__action-box {
		display: flex;
		align-items: center;
		gap: 5px;
	}
	&__game-box {
		position: relative;
		display: block;
		width: 100%;
		height: calc(100% - 45px);
		border: 5px solid var(--white-010);
		border-top: none;
		background: var(--black-050);
		& > iframe {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
		}
	}
	&__mobile {
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
	}
	&__mobile-btn-box {
		padding-top: 25px;
	}
	&__mobile-btn-row {
		display: flex;
		justify-content: center;
		align-items: center;
		flex-wrap: wrap;
		gap: 20px;
		padding: 20px 0;
	}
	@media screen and (min-width: 1200px) {
		height: 100vh;
	}
}
