.search-widget {
	--search-widget-bg: var(--bg-color);
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	z-index: 10;
	transform: translateY(-100%);
	transition: transform 0.2s ease-in-out;
	&--active {
		transform: translateY(0);
	}
	&__action-area {
		display: flex;
		justify-content: center;
		align-items: center;
		gap: 10px;
		padding: 0 10px;
		height: 50px;
		background: var(--search-widget-bg);
	}
	&__result-area {
		display: block;
		padding-top: 10px;
		width: 100%;
		max-height: calc(100vh - 50px);
		background: var(--search-widget-bg);
		overflow-y: auto;
		visibility: visible;
		&:empty {
			display: none;
			visibility: hidden;
		}
	}
	.search-area {
		max-width: 300px;
		&__input {
		}
		&__img {
		}
	}
}

