.betting-event {
	&__slider {
		display: none;
		visibility: hidden;
		&.slick-initialized {
			display: block;
			visibility: visible;
		}
		.slick-slide {
			padding: 0 3px;
		}
	}
	&__item {
		display: block;
		width: 300px;
		height: 200px;
		text-align: center;
		text-shadow: 0 1px 1px rgba(0, 0, 0, 0.6);
		overflow: hidden;
	}
	&__info-area {
		background: var(--white-005);
	}
	&__title {
		display: flex;
		justify-content: center;
		align-items: center;
		padding: 0 10px;
		width: 100%;
		height: 40px;
	}
	&__row {
		display: grid;
		grid-template-columns: 36% 28% 36%;
	}
	// &__row-item {
	// }
	&__player {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}
	&__player-img-box {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 80px;
		height: 80px;
		background: var(--white-020);
	}
	&__player-img {
		display: block;
		max-width: 52px;
		height: auto;
	}
	&__player-name {
		display: flex;
		justify-content: center;
		align-items: center;
		height: 30px;
	}
	&__game-info {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		height: 80px;
	}
	&__game-img {
		display: block;
		width: 48px;
		height: 44px;
	}
	&__game-time {
		font-size: 16px;
		font-weight: 700;
	}
	&__game-date {
		font-size: 12px;
	}
	&__betting-area {
		border-radius: 0 0 10px 10px;
	}
	&__link {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		height: 50px;
		font-size: 12px;
		color: var(--white-dark);
		background: var(--white-020);
		&:hover {
			color: var(--font-color);
			background: var(--decor-color);
		}
		b {
			font-size: 16px;
		}
	}
	@media screen and (min-width: 330px) {
		&__item {
			width: 310px;
		}
	}
	@media screen and (min-width: 350px) {
		&__slider {
			.slick-list {
				mask-image: linear-gradient(90deg,#fff,#fff 2%,#fff 97%,hsla(0deg,0%,100%,0));
				// padding-right: calc(100vw - 340px);
			}
		}
	}
