:root {
	// global colors
	--white: #fff;
	--white-dark: #eeeeee;

	--white-005: rgba(255,255,255, 0.05);
	--white-010: rgba(255,255,255, 0.1);
	--white-020: rgba(255,255,255, 0.2);

	--black: #121212;
	--black-050: rgba(18,18,18, 0.5);
	--black-080: rgba(18,18,18, 0.8);
	// --black-dark: #181818;

	--gray: #93939d;
	--gray-semi: #474749;
	--gray-dark: #323234;

	--yellow: #fecb2f;
	--yellow-dark: #e8b02b;
	// --yellow-light: #ffeb3b;

	--blue: #136bfb;
	--blue-light: #4289fc;
	--blue-dark: #185cc1;

	--green: #0b6665;
	--green-light: #1c4c4b;

	--red: #cf5153;

	// work colors
	--bg-color: var(--black);
	--font-color: var(--white);

	--decor-color: var(--blue);
	--decor-color-2: var(--yellow-dark);

	--error-color: var(--red);

	// work variables
	--header-height: 50px;
}
$decor-color: #136bfb;