.tournament {
	margin-bottom: 20px;
	// font-size: 12px;
	line-height: 1.667;
	color: var(--font-color);
	text-shadow: 0 1px 1px rgba(0, 0, 0, 0.6);
	&__grid {
		display: grid;
		justify-items: center;
		margin-bottom: 15px;
		padding: 10px;
		width: 100%;
		background: var(--white-010);
	}
	&__table-box {
		display: block;
		margin-bottom: 25px;
		width: 100%;
	}
	&__table-prev-link {
		display: inline-block;
		font-size: 12px;
		font-weight: 700;
		color: var(--decor-color);
		text-decoration: underline;
		cursor: pointer;
		&::after {
			content: "";
			display: inline-block;
			margin-left: 0.5em;
			width: 0;
			height: 0;
			border-style: solid;
			border-width: 4px 0 4px 4px;
			border-color: transparent transparent transparent
				var(--decor-color);
			transition: transform 0.2s ease-in-out;
		}
		&:hover {
			text-decoration: none;
			&::after {
				transform: translateX(0.2em);
			}
		}
	}

	&__info-main {
		margin-bottom: 1em;
		font-size: 14px;
		font-weight: 700;
	}
	&__table {
		margin-bottom: 20px;
		td,
		th {
			padding: 5px;
		}
	}
	.h1 {
		margin-bottom: 0.8em;
		text-align: center;
	}
	@media screen and (min-width: 550px) {
		&__table-row {
			display: flex;
			gap: 10px;
			& > * {
				flex: 1;
			}
		}
	}
	@media screen and (min-width: 760px) {
		&__grid {
			grid-template-columns: auto 1fr;
			// align-items: end;
			gap: 20px 10px;
		}
		&__table-box {
			grid-area: 2 / 1 / 3 / -1;
		}
	}
	@media screen and (min-width: 1200px) {
		&__grid {
			grid-template-columns: 300px 1fr auto;
			align-items: start;
			gap: 20px;
		}
		&__table-box {
			grid-area: unset;
			margin-bottom: 0;
			max-width: 600px;
		}
		&__info-box {
			display: block;
			max-width: 330px;
		}
	}
	@media screen and (min-width: 1500px) {
		&__info-box {
			max-width: 450px;
		}
	}
	@media screen and (min-width: 1900px) {
		font-size: 16px;
		&__grid {
			grid-template-columns: 360px 1fr auto;
			gap: 60px;
		}
		&__table-box {
			max-width: 700px;
		}
	}
}
