.action-area {
	position: fixed;
	bottom: 0;
	left: 0;
	width: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	gap: 10px;
	padding: 15px 0;
	background: rgba(18, 18, 18, 0.5);
		border-radius: 0;
		box-shadow: 0 4px 30px rgb(0 0 0 / 10%);
		backdrop-filter: blur(10px);
		-webkit-backdrop-filter: blur(10px);
	z-index: 4;
	.login-popup-trigger {
		display: none;
		visibility: hidden;
	}
	@media screen and (min-width: 600px) {
		display: none;
		visibility: hidden;
	}
	@media screen and (min-width: 1200px) {
		display: flex;
		visibility: visible;
		.login-popup-trigger {
			display: inline-flex;
            visibility: visible;
		}
	}
}
