.rules {
	display: flex;
	justify-content: center;
	font-size: 14px;
	line-height: 1.6667;
	color: var(--font-color);
	text-shadow: 0 1px 1px rgba(0, 0, 0, 0.6);
	&__body {
		display: block;
		padding: 20px 10px 0;
		max-width: 1030px;
		width: calc(100vw - 10px);
		background: var(--white-010);
	}
	&__inner {
		display: block;
		margin: 0 auto;
		max-width: 850px;
		width: 100%;
	}
	&__nav {
		position: relative;
		margin-bottom: 40px;
		font-size: 14px;
		line-height: 30px;
		font-weight: 700;
		color: var(--decor-color);
		z-index: 2;
		li {
			position: relative;
			a {
				color: inherit;
				text-decoration: underline;
				&:hover {
					text-decoration: none;
				}
			}
		}
	}
	&__info {
		margin-bottom: 2.5em;
		&--faq {
			padding-left: 10px;
		}
		h3,
		.h3 {
			margin-bottom: 0;
		}
		p {
			line-height: inherit;
		}
		a {
			color: var(--decor-color);
			text-decoration: underline;
			&:hover {
				text-decoration: none;
			}
		}
	}
	&__content {
		position: relative;
		z-index: 1;
		& > li {
			position: relative;
			margin-top: calc((var(--header-height) + 16px) * -1);
			padding-top: calc(var(--header-height) + 16px);
			&::before {
				position: absolute;
				top: calc(var(--header-height) + 16px);
				left: 0;
			}
			h3,
			.h3 {
				text-indent: 23px;
			}
		}
		li {
			margin-bottom: 2em;
			z-index: 1;
			ol {
				margin-top: 1em;
			}
			&:hover {
				z-index: 2;
			}
		}
		a {
			color: var(--decor-color);
			text-decoration: underline;
			&:hover {
				text-decoration: none;
			}
		}
	}
	h3,
	.h3 {
		display: inline-block;
		margin-bottom: 0;
		font-weight: 700;
		&::first-letter {
			text-transform: capitalize;
		}
		& + ol {
			margin-top: 0;
		}
	}
	ol {
		counter-reset: item;
	}
	ol > li {
		&::before {
			counter-increment: item;
			content: counter(item) ". ";
			display: inline-block;
			margin-right: 5px;
			font-size: 14px;
			font-weight: 700;
			color: var(--font-color);
		}
		ol {
			li::before {
				content: counters(item, ".") ". ";
				counter-increment: item;
			}
		}
		ul {
			padding-left: 10px;
			li {
				margin-bottom: 0;
				&::before {
					content: "";
					display: inline-block;
					margin-right: 5px;
					margin-bottom: 1px;
					width: 5px;
					height: 5px;
					background: var(--decor-color);
					border-radius: 50%;
				}
			}
		}
	}
	p + ul {
		margin-top: -1em;
	}
	ul {
		padding-left: 10px;
		li {
			margin-bottom: 0;
			&::before {
				content: "";
				display: inline-block;
				margin-right: 5px;
				margin-bottom: 1px;
				width: 5px;
				height: 5px;
				background: var(--decor-color);
				border-radius: 50%;
			}
		}
	}

	@media screen and (min-width: 600px) {
		&__body {
			padding: 20px 40px 0;
			width: calc(100vw - 40px);
		}
		&__info--faq {
			padding-left: 30px;
		}
	}
	@media screen and (min-width: 1200px) {
		font-size: 16px;
		&__nav {
			font-size: 16px;
			li::before {
				width: 30px;
			}
		}
		&__content {
			& > li {
				&::before {
					top: calc(var(--header-height) + 18px);
				}
			}
		}
	}
	@media screen and (min-width: 1900px) {
		&__body {
			max-width: 1240px;
		}
		&__inner {
			max-width: 950px;
		}
	}
}
