.news {
	&__slider {
		display: none;
		visibility: hidden;
		&.slick-initialized {
			display: block;
			visibility: visible;
		}
		.slick-slide {
			padding: 0 3px;
		}
	}
	&__item {
		display: block;
		width: 300px;
		overflow: hidden;
	}
	&__img-box {
		position: relative;
		display: block;
		width: 100%;
		height: 0;
		padding-bottom: 62%;
		background-color: var(--white-010);
		overflow: hidden;
		&:hover {
			.news__img {
				transform: scale(1.05);
			}
		}
	}
	&__img {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		object-fit: cover;
		transform: scale(1);
		transition: transform 0.3s ease-in-out;
	}
	&__info-box {
		display: block;
		padding: 20px;
		width: 100%;
		height: 144px;
		text-shadow: 0 1px 1px rgba(0, 0, 0, 0.6);
		background: var(--white-010);
	}
	&__title-link {
		display: -webkit-box;
		-webkit-line-clamp: 2;
		-webkit-box-orient: vertical;
		margin-bottom: 10px;
		width: 100%;
		height: 40px;
		font-size: 16px;
		font-weight: 700;
		color: var(--font-color);
		overflow: hidden;
		&:hover {
			color: var(--decor-color);
		}
	}
	&__info {
		display: -webkit-box;
		-webkit-line-clamp: 3;
		-webkit-box-orient: vertical;
		overflow: hidden;
		text-overflow: ellipsis;
		height: 54px;
		font-size: 14px;
	}
	@media screen and (min-width: 330px) {
		&__item {
			width: 310px;
		}
	}
	@media screen and (min-width: 350px) {
		&__slider {
			.slick-list {
				mask-image: linear-gradient(90deg,#fff,#fff 2%,#fff 97%,hsla(0deg,0%,100%,0));
			}
		}
	}
	@media screen and (min-width: 1900px) {
		&__slider {
			.slick-list {
				mask-image: none;
			}
		}
	}
}
