.main-nav {
	--main-nav-font-color: var(--font-color);
	--main-nav-link-bg: var(--white);
	position: relative;
	display: flex;
	justify-content: center;
    align-items: flex-end;
	width: 100%;
	// height: 40px;
	&__list {
		display: flex;
		justify-content: flex-start;
		align-items: flex-end;
		padding: 6px 5px 0;
		width: auto;
		overflow-x: auto;
	}
	&__item {
		display: flex;
		justify-content: center;
		align-items: center;
		padding: 0 5px;
	}
	&__link {
		display: inline-flex;
		justify-content: center;
		align-items: center;
		padding: 0 10px 0 0;
        width: auto;
		min-width: 100px;
		font-size: 12px;
		font-weight: 700;
		color: var(--main-nav-font-color);
		border-radius: 10px;
		transition: transform 0.1s ease-in-out;
		&:hover,
		&--active {
			background: var(--main-nav-link-bg);
			transform: translateY(-6px);
		}
		&--row-reverse {
			flex-direction: row-reverse;
            padding: 0 0 0 10px;
		}
		&--column {
			flex-direction: column;
            padding: 0 0 10px 0;
		}
		&--column-revers {
			flex-direction: column-reverse;
            padding: 10px 0 0 0;
		}
	}
	&__img {
		display: block;
		width: 40px;
		height: 40px;
	}
	&__info {
		display: inline-block;
		text-transform: capitalize;
        white-space: nowrap;
	}
}
